.project-name {
  display: inline-block;
  position: relative;
  font-size: 40px;
  font-weight: 100;
  letter-spacing: 6px;
}

.project-name:after {
  content: '';
  height: 1px;
  width: 40%;
  background-color: rgb(199, 194, 194);
  position: absolute;
  bottom: -0.2em;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
}

